<template>
	<view class="search-wrapper">
		<view class="search-header topbar">
			<input class="search-input" placeholder-class="phcolor" :placeholder="placeholder" @input="search" />
			<text class="iconfont icon-sousuo"></text>
			<view class="cancel" @tap="goBack">取消</view>
		</view>
		<view class="result-list">
			<view v-if="movies.length" class="movie-box">
				<view class="title">电影/电视剧/综艺</view>
				<navigator class="movie-section" :url="'/pages/subPages/movie-detail/movie-detail?movieId='+(movie.id)"
					v-for="(movie,index) in (movies)" :key="movie.id">
					<image class="movie-img" :src="movie.img"></image>
					<view class="movie-info">
						<view class="movie-name line-ellipsis">{{movie.nm}}
							<text :class="'version '+(movie.version)"></text>
						</view>
						<view class="line-ellipsis">{{movie.enm}}</view>
						<view class="line-ellipsis">{{movie.cat}}</view>
						<view class="line-ellipsis">{{movie.rt}}</view>
					</view>
					<view class="score">
						<text class="grade" v-if="movie.sc">{{movie.sc}}分</text>
						<text v-else>暂无评分</text>
					</view>
				</navigator>
			</view>
			<view v-if="cinemas.length">
				<view class="title">影院</view>
				<cinemaSection compName="cinemaSection" :data="{cinema}" v-for="(cinema,index) in (cinemas)" :key="cinema.id">
				</cinemaSection>
			</view>
		</view>
		<view v-if="!(movies.length || cinemas.length || !value)">
			<nothing compName="nothing" :data="{message:" 没有找到相关内容"}"></nothing>
		</view>
	</view>
</template>

<script>
	import cinemaSection from "../../../templates/cinemaSection/cinemaSection.vue";
	import nothing from "../../../templates/nothing/nothing.vue";
	export default {
		components: {
			cinemaSection,
			nothing
		},
		data() {
			return {
				value: '',
				stype: '',
				placeholder: '',
				movies: {},
				cinemas: {},

				movie: {
					id: "",
					img: "",
					version: "",
					enm: "",
					cat: "",
					rt: "",
					sc: ""
				},

				cinema: {
					id: ""
				}
			};
		},
		onLoad(query) {
			this.initPage(query);
		},
		methods: {
			initPage(query) {
				//搜索类型，-1代表搜索影院或电影，2代表搜索影院
				const stype = query.stype;
				let placeholder = '';
				if (stype === '-1') {
					placeholder = '搜电影、搜影院';
				} else {
					placeholder = '搜影院';
				}
				this.setData({
					stype,
					placeholder
				});
			},

			search(e) {
				const value = e.detail.value;
				const _this = this;
				this.setData({
					value
				});
				uni.request({
					url: `https://m.maoyan.com/ajax/search?kw=${value}&cityId=57&stype=${_this.stype}`,
					//没有获取猫眼城市ID的API，所以这里的城市ID只能写死，去掉城市ID会404
					success(res) {
						// if (res.data.movies) {
						// 	let movies = res.data.movies.list;
						// } else {
						// 	let movies = [];
						// }
						let movies = res.data.movies ? res.data.movies.list : []
						//
						movies = movies.map(item => {
							item.img = item.img.replace('w.h', '128.180');
							return item;
						});
						_this.setData({
							movies: movies,
							cinemas: res.data.cinemas ? res.data.cinemas.list : []
						});
					}
				});
			},

			goBack() {
				uni.navigateBack({
					delta: 1
				});
			}
		}
	};
</script>

